<template>
<div class="container mx-auto p-4">
  <div class="my-16 text-center p-4">
    <h1 class="text-3xl font-bold leading-tight mb-4">Calcolo di una proporzione</h1>
	<p>La formula per il calcolo di una proporzione è A : B = B : C.</p>
  </div>
  <div class="flex items-center justify-between">
		<div class="leading-none">
			<label class="md:-mr-12 md:-mt-10 font-bold" for="A">A</label>
			<input v-model="inputA" class="w-32 md:w-48 md:m-3 px-6 py-2 rounded-full shadow-md text-gray-800 text-xl bg-opacity-75 focus:outline-none" type="number" name="A" placeholder="es. 30">
			<p>Inserisci un valore per la A.</p>
		</div>
    <span class="text-4xl mx-6">:</span>
		<div class="leading-none">
			<label class="md:-mr-12 md:-mt-10 font-bold" for="B">B</label>
			<input v-model="inputB" class="w-32 md:w-48 md:m-3 px-6 py-2 rounded-full shadow-md text-gray-800 text-xl bg-opacity-75 focus:outline-none" type="number" name="B" placeholder="es. 20">
			<p>Inserisci un valore per la B.</p>
		</div>
  </div>
  <div class="flex justify-center md:mt-8"><span class="text-6xl">=</span></div>
  <div class="flex items-center justify-between">
		<div class="leading-none">
			<label class="md:-mr-12 md:-mt-10 font-bold" for="C">C</label>
			<input v-model="inputC" class="w-32 md:w-48 md:m-3 px-6 py-2 rounded-full shadow-md text-gray-800 text-xl bg-opacity-75 focus:outline-none" type="number" name="C" placeholder="es. 12">
			<p class="flex-none">Inserisci un valore per la C.</p>
		</div>
    <span class="text-4xl mx-6">:</span>
		<div class="leading-none">
			<label class="md:-mr-12 md:-mt-10 font-bold" for="D">D</label>
			<span class="block leading-6 w-32 md:w-48 md:m-3 px-6 py-2 bg-white rounded-full shadow-md text-xl focus:outline-none" :class="[inputD != 0 ? 'text-gray-800' : 'text-gray-500']">{{inputD != 0 ? inputD : 'es. 8'}}</span>
			<p>Il valore della D verrà calcolato.</p>
		</div>
  </div>
  <div class="flex justify-center my-16 duration-300">
    <button @click="isVisible = true" class="focus:outline-none rounded-full font-medium py-2 px-4 bg-blue-300 text-blue-800 capitalize shadow pointer">come funziona? 🤔</button>
  </div>
	<div v-if="isVisible != false" class="absolute bg-black flex inset-0 items-center justify-center bg-opacity-75">
		<div class="bg-white max-w-2xl p-10 rounded-lg shadow-2xl">
			<h2 class="font-medium mb-3 text-2xl">Spiegazione 🙋‍♂️</h2>
			<p class="mb-4">La proporzione è una formula utilizzata per trovare un valore incognito.</p>
			<p class="mb-4">Mettiamo per esempio che devi organizzare una serata con 3 amici e decidi di comprare una mega pizza e dividerla in 12 fette.</p>
			<p class="mb-4">Vuoi sapere quante fette di pizza spettano a ciascuno e il calcolo è facile: 12 (fette di pizza) diviso 4 (persone: 3 amici + tu!) e il risultato è 3.</p>
			<p class="mb-4">E se alla festa si aggiungesse un altro amico per un totale di 5 persone? In quante fette dovrebbe essere tagliata la pizza, per fare in modo che tutti e 5 possiate mangiare 3 fette di pizza ciascuno?</p>
			<p class="mb-4 font-medium">Usiamo una proporzione per calcolare il risultato: 4:12=5:X</p>
			<p class="mb-4">Si legge 4 sta a dodici come 5 sta a X, dove X è il numero di fette da tagliare.</p>
			<p class="mb-4">Per risolvere la proporzione si esegue il prodotto dei numeri vicini al simbolo uguale (detti medi), 12*5=20 e il risultato si divide per il restante numero noto, 4. Quindi 60/4=15.</p><p class="mb-4">Per fare in modo che 5 persone mangino 3 fette a testa, bisogna tagliare la mega pizza in 15 fette! Infatti 15/5=3.</p>
			<button @click="isVisible = false" class="focus:outline-none rounded-full font-medium py-2 px-4 bg-blue-300 text-blue-800 capitalize shadow pointer block mt-6 mx-auto">capito! 🤓</button>
		</div>
	</div>
</div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'App',
  data() {
    return {
      inputA: '',
      inputB: '',
      inputC: '',
			isVisible: false
    }
  },
  computed: {
    inputD: function(): string {
      if (this.inputA && this.inputB && this.inputC ) {
				const numberA = parseInt(this.inputA);
				const numberB = parseInt(this.inputB);
				const numberC = parseInt(this.inputC);
				const numberD = (numberB * numberC) / numberA;
        return numberD.toFixed(2);
       } else {
         return '';
       } 
    }
  }
});
</script>

<style lang="scss">
label {
  font-size: 6rem;
}
@media screen and (min-width: 768px) {
	label {
		font-size: 8rem;
	}
}
</style>
