
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'App',
  data() {
    return {
      inputA: '',
      inputB: '',
      inputC: '',
			isVisible: false
    }
  },
  computed: {
    inputD: function(): string {
      if (this.inputA && this.inputB && this.inputC ) {
				const numberA = parseInt(this.inputA);
				const numberB = parseInt(this.inputB);
				const numberC = parseInt(this.inputC);
				const numberD = (numberB * numberC) / numberA;
        return numberD.toFixed(2);
       } else {
         return '';
       } 
    }
  }
});
